"use client";
import Avatar from "@/app/_components/Avatar";
import { StyledCompanyButton } from "./styled";
import { Typography } from "@mui/material";
import FlexCenter from "@/app/_components/Layout/FlexCenter";
import ArrowUpwardIcon from "@/app/_assets/icons/ArrowUpwardIcon";
import getStorageDownloadLink from "@/app/_global/getStorageDownloadLink";

type CompanyButtonProps = {
  companyImage: string;
  companyName: string;
  companyId: string;
};

export function CompanyButton({
  companyImage,
  companyId,
  companyName,
}: CompanyButtonProps) {
  const img = getStorageDownloadLink(companyImage);
  return (
    <StyledCompanyButton>
      <Avatar src={companyImage} alt={"company-img"} size={"xs"} />
      <Typography variant="bodyMRegular">{companyName}</Typography>
      <FlexCenter>
        <ArrowUpwardIcon />
      </FlexCenter>
    </StyledCompanyButton>
  );
}
